<template>
  <wrapper>
    <v-row>
      <v-col cols="12">
        <h2 class="text--disabled">Órdenes del día</h2>
      </v-col>

      <template v-if="ordenesFiltradas && ordenesFiltradas.length > 0">
        <v-col v-for="(orden, indexOrden) in ordenesFiltradas" :key="indexOrden" cols="12">
          <v-card style="font-family: Nunito">
            <v-card-title class="mb-4">
              Cafetería {{ orden.cafeteria.nombre }}
              <v-spacer/>
              <span class="success--text">{{ orden.costo | moneda }}</span>
            </v-card-title>
            <v-card-text>
              <v-row>
                <v-col v-for="(producto, indexProducto) in orden.productos" :key="indexProducto" cols="12" md="6" class="mb-3">
                  <producto-en-orden :producto="producto" mostrar-mensaje-menu/>
                </v-col>
              </v-row>
            </v-card-text>
          </v-card>
        </v-col>
      </template>

      <v-row v-else align="center" justify="center" style="display: flex;justify-content: center;align-items: center;">
        <v-col cols="12" class="text-center" style="display: flex;justify-content: center;align-items: center;">
          <img src="../../assets/ordenes_estado_vacio.svg" :style="$vuetify.breakpoint.smAndDown ? 'width:100%' : 'width:50%'"/>
        </v-col>
        <v-col cols="12" class="text-center">
          <h2 style="font-family: Nunito;" class="text--disabled">No hay órdenes para hoy</h2>
        </v-col>
      </v-row>
    </v-row>

    <template v-if="$alumnoPuedeOrdenar()">
      <carrusel v-if="boletines.length > 0" :key="'boletines-' + boletines.length" titulo="Boletines" autoplay
                :ver-mas="() => (irA('DashboardBoletin'))">
        <carrusel-item v-for="(boletin, indexBoletin) in boletines" :key="indexBoletin">
          <boletin-resumen :boletin="boletin" redireccionar/>
        </carrusel-item>
      </carrusel>

      <carrusel :key="'categorias-' + categorias.length" titulo="Productos" :ver-mas="() => (irA('VistaProductosParaAlumno'))">
        <template v-if="categorias.length > 0">
          <carrusel-item v-for="(categoria, indexCategoria) in categorias" :key="indexCategoria">
            <categoria :categoria="categoria" to="VistaProductosParaAlumno"/>
          </carrusel-item>
        </template>

        <estado-vacio-en-dashboard v-else texto="No hay productos disponibles" img-productos/>
      </carrusel>

    </template>
  </wrapper>
</template>

<script>
  import CarruselItem from "../General/Carrusel/CarruselItem";
  import Carrusel from "../General/Carrusel/Carrusel";
  import ProductoEnOrden from "../Ordenes/ProductoEnOrden";
  import FechaService from "../../services/FechaService";
  import BoletinService from "../../services/BoletinService";
  import BoletinResumen from "../BoletinEscolar/BoletinResumen";
  import CategoriasService from "../../services/CategoriasService";
  import EstadoVacioEnDashboard from "../General/EstadoVacioEnDashboard";
  import Categoria from "../Tienda/Categoria";
  import OrdenesService from "../../services/OrdenesService";

  export default {
    name: "DashboardAlumno",
    components: {Categoria, EstadoVacioEnDashboard, BoletinResumen, ProductoEnOrden, Carrusel, CarruselItem },

    data(){
      return{
        productos: [],
        boletines: [],
        menus: [],
        cafeterias: [],
        ordenes: [],
        ordenesFiltradas: [],
        categorias: []
      }
    },
    async created() {
      this.$loader = true
      try {
        this.$set(this.$usuario.cafeteria, 'saldoAlumno',
          this.$getSaldoDeAlumnoParaCafeteria(this.$usuario.cafeteria.id, this.cafeterias))
      } catch (e) {
        this.$showAlerta('600', 'error', 'mdi-alert-circle',
          'No fue posible obtener sus saldos.', true)
      }

      try {
        this.ordenes = await OrdenesService.getPorIdAlumnoAndEstatus(this.$usuario.id, 'CREADA')
        this.filtrarOrdenes()
      }catch (e) {
        this.ordenes = []
      }

      try {
        this.boletines = await BoletinService.getBoletines(this.$usuario.idCliente)
      } catch (e) {
        this.boletines = []
      }

      try {
        this.categorias = await CategoriasService.getCategorias();
      } catch (e) {
        this.categorias = []
      }
      this.$loader = false
    },

    methods: {
      filtrarOrdenes() {
        const fecha = FechaService.getFechaString();
        this.ordenesFiltradas = this.ordenes.filter(orden => orden.fecha === fecha);
      },
      irA(componente){
        this.$router.push({ name: componente })
      }
    }

  }
</script>

<style scoped>

</style>
