<template>
  <wrapper>
    <carrusel v-if="boletines.length > 0" :key="'boletines-' + boletines.length" titulo="Boletines" autoplay
              :ver-mas="() => (irA('DashboardBoletin'))">
      <carrusel-item v-for="(boletin, indexBoletin) in boletines" :key="indexBoletin">
        <boletin-resumen :boletin="boletin" redireccionar/>
      </carrusel-item>
    </carrusel>

    <carrusel :key="'menus-' + menus.length" titulo="Menus" :ver-mas="() => (irA('DashboardMenu'))">
      <template v-if="menus.length > 0">
        <carrusel-item v-for="(menu, indexMenu) in menus" :key="indexMenu">
          <tarjeta-menu :menu="menu" :to="{ name: 'DashboardMenu' }" redireccionar/>
        </carrusel-item>
      </template>

      <estado-vacio-en-dashboard v-else texto="No hay menús disponibles" img-menu/>
    </carrusel>

    <carrusel :key="'categorias-' + categorias.length" titulo="Productos" :ver-mas="() => (irA('DashboardTienda'))">
      <template v-if="categorias.length > 0">
        <carrusel-item v-for="(categoria, indexCategoria) in categorias" :key="indexCategoria">
          <categoria :categoria="categoria" to="DashboardTienda"/>
        </carrusel-item>
      </template>

      <estado-vacio-en-dashboard v-else texto="No hay productos disponibles" img-productos/>
    </carrusel>

    <carrusel :key="'hijos-' + $usuario.hijos.length" titulo="Alumnos" :ver-mas="() => (irA('Alumnos'))">
      <template v-if="$usuario.hijos.length > 0">
        <carrusel-item v-for="(alumno, indexAlumno) in $usuario.hijos" :key="indexAlumno">
          <alumno :alumno="alumno.alumno" redireccionar/>
        </carrusel-item>
      </template>

      <estado-vacio-en-dashboard v-else texto="Aún no ha agregado alumnos" img-ninos/>
    </carrusel>
  </wrapper>
</template>

<script>
  import Carrusel from "./../General/Carrusel/Carrusel";
  import CarruselItem from "./../General/Carrusel/CarruselItem";
  import BoletinService from "../../services/BoletinService";
  import MenusService from "../../services/MenusService";
  import TarjetaMenu from "./../Menu/TarjetaMenu";
  import Alumno from "./../Alumnos/Alumno";
  import EstadoVacioEnDashboard from "./../General/EstadoVacioEnDashboard";
  import CategoriasService from "../../services/CategoriasService";
  import Categoria from "./../Tienda/Categoria";
  import BoletinResumen from "./../BoletinEscolar/BoletinResumen";

  export default {
    name: "DashboardPadreDeFamilia",
    components: {BoletinResumen, Categoria, EstadoVacioEnDashboard, Alumno, TarjetaMenu, CarruselItem, Carrusel},

    data(){
      return{
        categorias: [],
        boletines: [],
        menus: []
      }
    },
    async created() {
      this.$loader = true
      try {
        this.categorias = await CategoriasService.getCategorias();
      } catch (e) {
        this.categorias = []
      }
      try {
        this.menus = await MenusService.getMenusDisponibles()
      } catch (e) {
        this.menus = []
      }
      try {
        this.boletines = await BoletinService.getBoletines(this.$usuario.idCliente)
      } catch (e) {
        this.boletines = []
      }
      this.$loader = false
    },
    methods: {
      irA(componente){
        this.$router.push({ name: componente })
      }
    }
  }
</script>

<style scoped>

</style>
