import AuthConst from '../config/AuthConst';
import http from "./http";

const getBoletines = (idCliente) => {
  let url = AuthConst.BASE_URL + '/boletines-escolares/cliente/' + idCliente;
  return http.get(url)
};

const crearBoletin = (boletin) => {
  let url = AuthConst.BASE_URL + '/boletines-escolares';
  return http.post(url, boletin)
};

const updateBoletin = (boletin) => {
  let url = AuthConst.BASE_URL + '/boletines-escolares';
  return http.put(url, boletin)
}

const deleteBoletines = (idBoletin) => {
  let url = AuthConst.BASE_URL + '/boletines-escolares/'+ idBoletin;
  return http.remove(url)
};


export default { getBoletines, crearBoletin, updateBoletin, deleteBoletines }
